<template>
  <div class="index">
    <div class="container">
		     <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item, key) in bannerList" :key="key"><img :src= item.picture></div>
                </div>
            </div>
            <div class="pro-box"  v-for="(item, key) in proList" :key="key" @click = "productDetail(item)">
            	<div class="name-box">
            		<img :src = item.adsIco  class="pro-logo">
            		<div class="pro-name">{{item.bountyName}}</div>
            		<div class="pro-score">{{item.score}}</div>
            		<img src="../assets/images/rate.png" class="score">
            	</div>
            	<div class="pro-detail">
            		<div class="detail-t">
            			<div class="detail-tl">
            				<div class="loan-money">
            					<span class="min-money">{{item.loanMinMoney}}</span> ~
            					<span class="max-money">{{item.loanMaxMoney}}</span>
            				</div>
            				<div class="money-detail">Kuota(juta Rp)
							</div>
            			</div>
            			<div class="detail-tm">
            				<div ><img src="../assets/images/money_rate.png"><span class="money-rate">{{item.loanMinRate}}%</span>/Hari</div>
            				<div><img src="../assets/images/time.png"><span class="money-check">{{item.lendersDay}}</span></div>
            			</div>
            			<div class="detail-tr">GO</div>
            			<img src="../assets/images/red_more.png" class="detail-right">
            		</div> 
            	</div>
            	<div class="detail-b">
            			{{item.bountyDsc}}
            	</div>
            </div>
	</div>
  </div> 
</template>

<script>
import Swiper from 'swiper';
export default {
  name: 'index',
  data () {
    return {
      bannerList:[],
      proList:[]
    }
  },
  mounted(){
    var vm = this; 
      // 
      this.$ajax({
          method: 'post',
          url: 'http://www.loansface.com/superapi/ut/banner'
        }).then(function(res){
          vm.bannerList = res.data.data
          vm.$nextTick(function () {
        vm.loopBanner();
      })
        });
        // 
        vm.$ajax({
          method: 'post',
          url: 'http://www.loansface.com/superapi/s/bounty/list'
        }).then(function(res){
          vm.proList = res.data.data
        });
    
  },
  methods: {
    loopBanner(){ 
        new Swiper('.swiper-container', {
            autoplay: true, //
            loop: true,
        });
    },
    productDetail(item){
      var vm = this;
      this.$router.push({name: 'productdetail',
      params:{'bountyId':item.id,
      'money':item.loanMinMoney,
      'limit':item.limitDsc,
      'bountyName':item.bountyName,
      'score':item.score,
      'loanMinMoney':item.loanMinMoney,
      'loanMaxMoney':item.loanMaxMoney,
      'loanMinRate':item.loanMinRate,
      'lendersDay':item.lendersDay,
      'bountyDsc':item.bountyDsc,
      'adsIco':item.adsIco,
      'loanLimitMax':item.loanLimitMax,
      'loanLimit':item.loanLimit,
      'adsUrl':item.adsUrl
      }});
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 @import '../assets/css/base.css';
</style>



