        		<div class="loan-bl">
        			<div class="loan-blt">Rp {{bList.fee}}</div>
        			<div class="loan-blb">Bunga + admin</div>
        		</div>
        	</div>
        </div>
        <div class="reward-box">
        	<div class="title"> Penjelasan misi Reward</div>
        	<div class="reward-des">
        		<img src="../assets/images/red_ring.png" class="red-cir">
        		<div class="des-detail">Download APP dan meminjam, mendapat bonus sebesar: Rp. 690,-</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/red_ring.png" class="red-cir">
        		<div class="des-detail">Merekomendasikan teman mendownload dan meminjam mendapat bonus sebesar: Rp. 690,- *2</div>
        	</div>
