        		<img src="../assets/images/red_ring.png" class="red-cir">
        		<div class="des-detail">Teman kamu merekomendasikan orang lain medownload APP  mendapat bonus sebesar: Rp. 690,- *4</div>
        	</div>
        </div>
        <div class="reward-box">
        	<div class="title"> Proses peminjaman</div>
        	<div class="reward-des">
        		<img src="../assets/images/down.png" alt="" class="des-img1">
        		<div class="des-detail">unduh program aplikasi</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/authentication.png" alt="" class="des-img1">
        		<div class="des-detail">Akun registerasi, lengkapi sertifikasi nama asli</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/write_info.png" alt="" class="des-img1">
        		<div class="des-detail">Verifikasi persyaratan informasi, isi informasi</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/wait.png" alt="" class="des-img1">
