        	</div>
        </div>
        <div class="reward-box reward-box2">
        	<div class="title"> Penjelasan misi Reward</div>
        	<div class="reward-des">
        		<img src="../assets/images/ic_cam.png" alt="" class="des-img1">
        		<div class="des-detail2">
        			<div class="des-title">Verifikasi</div>
        			<div class="des-content">No Ponsel & No Verifikasi</div>
        		</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/ic_doller.png" alt="" class="des-img1">
        		<div class="des-detail2">
        			<div class="des-title">Memilih kuota pinjaman</div>
        			<div class="des-content">Pilih kuota pinjaman & Batas pengembalian</div>
        		</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/ic_id.png" alt="" class="des-img1">
        		<div class="des-detail2">
        			<div class="des-title">Melengkapi Data Identitas</div>
        			<div class="des-content">Meliputi : Nama yg tercantum pada KTP, No KTP, Jenis Kelamin, Tempat Lahir, Tanggal Lahir, Suku, Pendidikan, Status Perkawinan, Jumlah tabungan, Alamat, Provinsi,Kota/Kabupaten, Kecamatan,Kelurahan, Kontak Perusahaaan, Lama Bekerja, Gaji per bulan, Kontak darurat sebanyak 2 org: Nama,No Ponsel</div>
        		</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/id_id2.png" alt="" class="des-img1">
        		<div class="des-detail2">
        			<div class="des-title">Foto KTP</div>
        			<div class="des-content">Selfie memegang KTP</div>
        		</div>
        	</div>
        	<div class="reward-des">
        		<img src="../assets/images/ic_card.png" alt="" class="des-img1">
        		<div class="des-detail2">
        			<div class="des-title">Data Rekening</div>
        			<div class="des-content">Nama Bank, Nomor Rekening Bank, Nama Pemilik Rekening, Bank Pembayaran</div>
        		</div>
        	</div>
        </div>
		<div class="down-btn" @click="AccountKit()">Pinjam</div>
	<div class="bg-alert-box" v-bind:class="{ active: isActive }" >
		<div class="alert-box">
			<div class="button-t">
				<div class="button-l" @click="changeAlert()">Batal</div>
				<div class="button-r"  @click="changeAlertg()">Konfirmasi</div>
			</div>
			<div class="num-box">
				<div class="num" v-for="(item1, key) in numList" :key="key" v-bind:class="{ activeC: isActiveC }" @click="changecolor($event)">{{item1}}</div>
			</div>
			
		</div>
	</div>
	</div>
</template>

<script>
import Qs from 'qs';
export default {
  name: 'productdetail',
  data () {
    return {
      routerParams:{},
      loanMinMoney:0,
      loanMaxMoney:0,
	  bList:[],   //
	  minMax:[],	// 
	  days:[],     //
	  numList:[] ,//
	  isActive:true,
	  isActiveC:false,
	  isDays:false
    }
  },
  mounted(){
    var vm = this; 
	vm.getParams();
     this.$ajax({
          method: 'post',
          url: 'http://www.loansface.com/superapi/ut/calculation?bountyId='+vm.routerParams.bountyId+'&money='+vm.loanMinMoney+'&limit='+vm.routerParams.loanLimit
        }).then(function(res){
          vm.bList = res.data.data;
        });
  },
  methods: {
	AccountKit(){
		var vm = this;
		const dataB = JSON.parse(sessionStorage.getItem('IsLogin'))
		console.log(dataB);
		if (dataB) {
			var data = Qs.stringify({
            code:dataB.code,
            bountyId: vm.routerParams.bountyId,
            appSecret:dataB.appSecret,
            userAccessToken:dataB.userAccessToken
          });
      this.$ajax({
          method: 'POST',
          url: 'http://www.loansface.com/superapi/ut/signIn',
          data:data
        }).then(function(res){
          const orderData = { 'appSecret': res.data.data.appSecret, 'userAccessToken': res.data.data.userAccessToken } 
          console.log(orderData);
          sessionStorage.setItem('IsLogin', JSON.stringify(orderData));
          window.location.href = vm.routerParams.adsUrl;
        });
			window.location.href = vm.routerParams.adsUrl;
		} else {
			this.$router.push({name: 'faccountKit',
			params:{'bountyId':vm.routerParams.bountyId,
			'adsUrl':vm.routerParams.adsUrl
		}});
		}      	
	},
    getParams(){
        // 
        var vm = this;
        let routerParams = this.$route.params
        vm.routerParams =routerParams;
        vm.loanMinMoney = parseInt(routerParams.loanMinMoney*1000000);
		vm.loanMaxMoney = parseInt(routerParams.loanMaxMoney*1000000);
		let minM = vm.loanMinMoney/100000;
		let maxM = vm.loanMaxMoney/100000;
		for(let a = minM; a <= maxM; a++){
			let m = a * 100000;
			vm.minMax.push(m);
		}
		for(let b = parseInt(routerParams.loanLimit); b <= parseInt(routerParams.loanLimitMax); b++){
			vm.days.push(b);
		}
	},
	changeAlert(){
		var vm = this;
		vm.isActive =true;
		$('.loan-money2').text(vm.routerParams.loanLimit+'Hair');
		$('.loan-money1').text('Rp'+vm.loanMinMoney);
	},
	changeAlertg(){
		var vm = this;
		let day =parseInt($('.loan-money2').text());
		let money =parseInt($('.loan-money1').text().substr(2));
		this.$ajax({
          method: 'post',
          url: 'http://www.loansface.com/superapi/ut/calculation?bountyId='+vm.routerParams.bountyId+'&money='+money+'&limit='+day
        }).then(function(res){
          vm.bList = res.data.data;
		  vm.isActive =true;
        });
	},
	changeMinMax(){
		var vm = this;
		vm.isActive =false;
		vm.numList = vm.minMax;
		vm.isDays = false;
	},
	changeDays(){
		var vm = this;
		vm.numList = vm.days;
		vm.isActive =false;
		vm.isDays = true;
	},
	changecolor(event){
		var vm = this;
		var el = event.currentTarget;
		$('.num').removeClass('activeC');
		el.className = 'num activeC';
		if(vm.isDays){
			$('.loan-money2').text(el.innerHTML+'Hair');
		}else{
			$('.loan-money1').text('Rp'+el.innerHTML);
		}
		
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.active{
		display: none;
	}
	.activeC{
		color: #F8564E;
	}
