    	background-color: #f0f0f0;
    }
    .pro-box1{
    	background-color: #fff;
    	padding-bottom: .26666667rem;
    }
    .name-box{
    width: 100%;
    height: 1.38666667rem/* 52px */;
    display: flex;
    align-items: center;
    padding:.26666667rem .4rem;
    box-sizing: border-box;
}
