			color: #999999;
		}
		/*  */
		.loan-box{
			width: 100%;
			height: 4.45333333rem/* 167px */;
			background-color: #fff;
			margin-top: .26666667rem;
			padding: .42666667rem/* 16px */ .26666667rem;
