			color: #666666;
		}
		.bg-alert-box{
			width: 100%;
			height: 100%;
			position: fixed;
			z-index: 10;
			top: 0;
			left: 0;
			background: rgba(0,0,0,0.44);
		}
		.alert-box{
			width: 100%;
