import Vue from 'vue'
import Router from 'vue-router'
import login from '../components/login'
import index from "../components/index";
import productdetail from "../components/productdetail";
import faccountKit from '../components/faccountKit';
Vue.use(Router)


export default new Router({
    // mode: 'history',
    routes: [{
            path: '/login',
            name: 'login',
            component: login
        },
        {
            path: '/index',
            name: 'index',
            component: index
        },
        {
            path: '/productdetail',
            name: 'productdetail',
            component: productdetail
        },
        {
            path: '/faccountKit',
            name: 'faccountKit',
            component: faccountKit
        }
    ]
})


