<template>
  <div class="account-kit">
    <facebook-account-kit ref="accountKit"
      appId="462525834236236"
      version="v1.1"
      :fbAppEventsEnabled='true'
      :debug='true'
      :loginType='loginType'
      state="dsffd44ad65654fda64fd4a">
      <!-- <select v-model="loginType">
        <option disabled value="">choose one</option>
        <option value="EMAIL">EMAIL</option>
        <option value="PHONE">PHONE</option>
      </select> -->
      <div>
        <input value="+1" id="country_code" v-model="countryCode" />
        <input placeholder="phone number" id="phone_number" v-model="phoneNumber"/>
        <button @click="login()">Login</button>
      </div>
      <!-- <div v-if="loginType == 'EMAIL'">
        <input placeholder="email" id="email" v-model="emailAddress"/>
      </div> -->
     
    </facebook-account-kit>
   
    <router-view/>
  </div>
</template>

<script>
import Qs from 'qs';
export default {
  name: 'accountkit',
  data() {
    return {
      loginType: "PHONE",
      emailAddress: "",
      countryCode: "+",
      phoneNumber: "",
      bountyId: '',
      downUrl:''
    };
  },
  mounted(){
    var vm = this; 
    vm.getParams();
  },
  methods: {
    getParams(){
        // 
        var vm = this;
        let routerParams = this.$route.params
        console.log(routerParams);
        vm.routerParams =routerParams;  
        vm.bountyId = parseInt(vm.routerParams.bountyId);
	},
    login() {
      console.log(this.$refs);
      this.$refs.accountKit.login(
        {
          countryCode: this.countryCode,
          phoneNumber: this.phoneNumber,
          emailAddress: this.emailAddress,
          // display: "modal"
        },
        this.loginCallback
      );
    },
    loginCallback(response) {
      if (response.status === "PARTIALLY_AUTHENTICATED") {
      var code = response.code;
      var csrf = response.state;     
      var vm = this;      
      var data = Qs.stringify({
            code:code,
            bountyId: vm.bountyId,
            appSecret:'',
            userAccessToken:''
          });
      this.$ajax({
          method: 'POST',
          url: 'http://www.loansface.com/superapi/ut/signIn',
          data:data
        }).then(function(res){
          const orderData = {'code':code, 'appSecret': res.data.data.appSecret, 'userAccessToken': res.data.data.userAccessToken } 
          console.log(orderData);
          sessionStorage.setItem('IsLogin', JSON.stringify(orderData));
          window.location.href = vm.routerParams.adsUrl;
        });
      // Send code to server to exchange for access token
    }
    else if (response.status === "NOT_AUTHENTICATED") {
      // handle authentication failure
    }
    else if (response.status === "BAD_PARAMS") {
      // handle bad parameters
    }
     
    }
  }
}
</script>

<style>
.account-kit div{
        width: 100%;
        height: 8rem/* 300px */;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
        margin-top: 1.5rem;
	}
	#country_code,#phone_number{
		width: 50%;
		height: 1.33333333rem/* 50px */;
		outline: none;
		font-size: .42666667rem/* 16px */;
		border: 1px solid #666;
		margin-bottom: .53333333rem/* 20px */;
	}
	#phone_number{
		
	}
	button{
		width: 50%;
		color: red;
		height: 1.06666667rem/* 40px */;
		line-height:1.06666667rem ;
		font-size: .42666667rem/* 16px */;
		border-radius: .26666667rem;
		outline: none;
	}
</style>



